<template>
  <!--  Modal Progress bar -->
  <div>
    <div id="video-overlay" class="video-overlay" :class="videoOverlay == true?'open': 'hide'">
      <article>
        <h2>Uploading...</h2>
        <h3>{{percentText}} %</h3>
        <input type="radio" name="switch-color" class="hide" id="cyan" checked />
        <div class="chart">
          <div class="bar" :class="percentClass">
            <div class="face top">
              <div class="growing-bar"></div>
            </div>
            <div class="face side-0">
              <div class="growing-bar"></div>
            </div>
            <div class="face floor">
              <div class="growing-bar"></div>
            </div>
            <div class="face side-a"></div>
            <div class="face side-b"></div>
            <div class="face side-1">
              <div class="growing-bar"></div>
            </div>
          </div>
        </div>
      </article>
    </div>
  </div>
  <!-- Progress bar -->
</template>

<script>
/* Event bus to handle communication between components */
export const eventProgress = new Vue();
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
Vue.use(VueAxios, axios);

export default {
  data: function() {
    return {
      modal: true,
      percentClass: "bar-0",
      percentText: 0,
      videoOverlay: false
    };
  },
  mounted() {
    eventProgress.$on(
      "new-progress",
      function() {
        this.videoOverlay = true;
      }.bind(this)
    );
    eventProgress.$on(
      "new-percent",
      function(response) {
        this.percentText = response;
        this.percentClass = "bar-" + response;
      }.bind(this)
    );
    eventProgress.$on(
      "finish-progress",
      function() {
        this.videoOverlay = false;
        this.percentText = 0;
        this.percentClass = "bar-" + 0;
      }.bind(this)
    );
  }
};
</script>

